@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
    @apply transition-colors duration-300;
  }
  
  /* Ensure smooth transitions for dark mode */
  * {
    @apply transition-colors duration-300;
  }
}

@layer components {
  .btn-primary {
    @apply bg-blue-600 hover:bg-blue-700 dark:bg-yellow-600 dark:hover:bg-yellow-700 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-200 shadow-lg hover:shadow-xl;
  }
  
  .btn-secondary {
    @apply bg-white hover:bg-blue-50 dark:bg-gray-800 dark:hover:bg-gray-700 text-blue-600 dark:text-yellow-400 font-semibold py-3 px-6 rounded-lg border-2 border-blue-600 dark:border-yellow-500 transition-all duration-200;
  }
  
  .card {
    @apply bg-white/80 dark:bg-gray-800/80 backdrop-blur-sm rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1 border border-blue-100 dark:border-gray-700;
  }
  
  .section-bg {
    @apply bg-gradient-to-br from-blue-50/50 to-white dark:from-gray-900/50 dark:to-gray-800;
  }
  
  .section-bg-alt {
    @apply bg-gradient-to-br from-white to-blue-50/30 dark:from-gray-800 dark:to-gray-900/30;
  }
}